import React from "react"
import sectionStyles from "./bodySection.module.css"

export default props => (
  <section
    id={"section-" + props.sectionID}
    className={(props.sectionStyle==="grey")?sectionStyles.sectionGrey:sectionStyles.sectionWhite}
    >
    {props.sectionHdr &&
      <h2>{props.sectionHdr}</h2>
    }

    {props.children}
  </section>
)
